body {
	background: linear-gradient(90deg, #721B8C 0%, #861FA6 100%);
}

#particles-js {
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
}

.flex-container {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	margin-top: 1.5vh;
	user-select: none;
	-webkit-user-drag: none;
}

.item {
	margin-right: 2.75vh;
	user-select: none;
	-webkit-user-drag: none;
}

.logo {
	pointer-events: auto;
	position: relative;
	content: url("logo.png");
	width: 25vh;
	user-select: none;
	-webkit-user-drag: none;
}

.vertical-center {
	margin: 0;
	position: absolute;
	top: 50%;
	left: 50%;
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	pointer-events: none;
}

a {
	pointer-events: auto;
	color: white;
	font-family: Verdana;
	font-size: 4vh;
	text-decoration: none;
	transition: color 1s;
	outline: none;
	user-select: none;
	-webkit-user-drag: none;
}

a:hover {
	color: #8b8b8b;
}

i {
	color: white;
	font-family: Verdana;
	font-size: 3.5vh;
	text-decoration: none;
	margin-right: 0.75vh;
	user-select: none;
	-webkit-user-drag: none;
}
